import React from "react";
import { useInView } from "react-intersection-observer";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faTrash } from "@fortawesome/free-solid-svg-icons";
import { Col, FormGroup, Input, Row } from "reactstrap";
import parse from "html-react-parser";
import { useNavigate } from "react-router-dom";

const ProfileSkillsSection = (props) => {
  const navigate = useNavigate();
  const item = props.item;
  const { ref, inView } = useInView({
    triggerOnce: true, // Only trigger the animation once
    threshold: 0.1, // Percentage of section visibility to trigger the animation
  });

  const manageQnA = (skillId) => {
    navigate(`/skillsQnA/${skillId}`);
  };

  return (
    <div ref={ref} className={inView ? "animated-section" : "hidden-section"}>
      <Row className="pe-5 pe-md-0">
        {item.skillImage && (
          <Col className="col-3 col-md-1">
            <img
              src={`/uploads/${item.skillImage}`}
              className="mx-1 mw-100 p-1 border border-warning"
              alt={item.skillName}
            />
          </Col>
        )}
        <Col className="flex-grow-1">
          <h3 className="t-color f-size-20">
            {item.skillName}{" "}
            <span
              className="cursor-pointer f-size-15 p-color"
              onClick={(e) => manageQnA(item._id)}
            >
              (Add Q n A)
            </span>
          </h3>
          <h3 className="mb-1 f-size-15 f-bold t-color-e5">
            {item.skillHeading}
          </h3>
          <div className="f-size-15 t-color-c5">
            {parse(`${item.skillDesc}`)}
          </div>
        </Col>
      </Row>
      <div
        className="edit-delete d-flex flex-column flex-md-row"
        style={{ right: "0px" }}
      >
        <FormGroup switch className="align-self-center">
          <Input
            role="switch"
            type="switch"
            checked={item.active}
            onChange={(e) => props.updateStatus(item._id, !item.active)}
          />
          <span className="d-none d-md-inline">
            {item.active ? "Active" : "Inactive"}
          </span>
        </FormGroup>
        <FontAwesomeIcon
          icon={faEdit}
          onClick={(e) =>
            props.editSkills(
              item.skillName,
              item.skillHeading,
              item.skillImage,
              item.skillDesc,
              item._id
            )
          }
          className="mt-1"
          style={{ padding: "7px 7px" }}
        />
        <FontAwesomeIcon
          icon={faTrash}
          onClick={(e) => props.deleteSkills(item._id)}
          className="mt-1"
          style={{ padding: "7px 7px" }}
        />
      </div>
    </div>
  );
};

export default ProfileSkillsSection;
