import React from "react";
import { Container } from "reactstrap";
import Skills from "../../shared-components/Skills";

const SkillsCategories = () => {
  return (
    <>
      <Container className="t-color skill-category">
        <Skills displayType="block" updateSkill={true} />
      </Container>
    </>
  );
};

export default SkillsCategories;
