import React from "react";
import { Container } from "reactstrap";
import parse from "html-react-parser";
import AnimatedElement from "../../shared-components/AnimatedElement";

const AsProfessional = (props) => {
  return (
    <>
      {props.content.active && (
        <Container className="text-center">
          <AnimatedElement>
            <h1 className="p-color f-size-20 py-4 mt-5">
              {parse(`${props.content.contentTitle}`)}
              <hr className="w-200px my-4 mx-auto" />
            </h1>
            <h3 className="t-color f-size-36 py-3">
              {parse(`${props.content.contentHeading}`)}
            </h3>
            <div className="t-color f-size-20 pb-5">
              {parse(`${props.content.contentData}`)}
            </div>
          </AnimatedElement>
        </Container>
      )}
    </>
  );
};

export default AsProfessional;
