import React, { useEffect, useState } from "react";
import Base from "../Base";
import Aboutme from "../components/HomeView/Aboutme";
import AsProfessional from "../components/HomeView/AsProfessional";
import HomeBanner from "../components/HomeView/HomeBanner";
import HomeMiddleBanner from "../components/HomeView/HomeMiddleBanner";
import ServiceCategories from "../components/HomeView/ServiceCategories";
import { useDispatch, useSelector } from "react-redux";
import { getHomeData } from "../reducers/homeReducer";
import axios from "axios";
import SkillsCategories from "../components/HomeView/SkillsCategories";
import { useNavigate } from "react-router-dom";
import { Button, Col, Row } from "reactstrap";

const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";

const Home = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);

  const [homeData, setHomeData] = useState({});
  const [userProfile, setUserProfile] = useState({});

  const getHomeScreenData = async () => {
    const data = await dispatch(
      getHomeData({ token: userdetails.token ? userdetails.token : null })
    );
    if (!data.error) {
      await setHomeData(data.payload);
    } else {
      await setHomeData({});
    }
  };

  useEffect(() => {
    const getUserData = async () => {
      const { data } = userdetails.token
        ? await axios.get(`${API_URL}/api/user/info/${userdetails.token}`)
        : await axios.get(`${API_URL}/api/user/info`);
      if (!data.error) {
        await setUserProfile(data);
      } else {
        await setUserProfile({});
      }
    };
    getUserData();
    getHomeScreenData();
  }, []);
  return (
    <>
      <Base>
        {homeData.banner &&
          homeData.pageContent1 &&
          homeData.services &&
          homeData.middleBanner &&
          homeData.pageContent2 &&
          homeData.skills &&
          !homeData.banner.active &&
          !homeData.pageContent1.active &&
          !homeData.services.active &&
          !homeData.middleBanner.active &&
          !homeData.pageContent2.active &&
          !homeData.skills.active && (
            <div className="container-fluid px-0">
              <Row>
                <Col className="text-center p-4">
                  <Button
                    className="sp-btn-secondary rounded-0 px-5"
                    onClick={() => navigate("/profile")}
                  >
                    Please Update Profile
                  </Button>
                </Col>
              </Row>
            </div>
          )}
        <div className="container-fluid px-0">
          {homeData.banner && homeData.banner.active ? (
            <HomeBanner banner={homeData.banner} userProfile={userProfile} />
          ) : (
            ""
          )}
          {homeData.pageContent1 && homeData.pageContent1.active ? (
            <Aboutme aboutme={homeData.pageContent1} />
          ) : (
            ""
          )}
          {homeData.services && homeData.services.active ? (
            <ServiceCategories services={homeData.services} />
          ) : (
            ""
          )}
          {homeData.middleBanner && homeData.middleBanner.active ? (
            <HomeMiddleBanner middleBanner={homeData.middleBanner} />
          ) : (
            ""
          )}
          {homeData.pageContent2 && homeData.pageContent2.active ? (
            <AsProfessional content={homeData.pageContent2} />
          ) : (
            ""
          )}
          {homeData.skills && homeData.skills.active ? (
            <SkillsCategories skills={homeData.skills} />
          ) : (
            ""
          )}
        </div>
      </Base>
    </>
  );
};

export default Home;
